import React from "react";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import { motion } from "framer-motion";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWrench } from "@fortawesome/free-solid-svg-icons";

const ProjectModal = (props) => {
	console.log("props.index", props.index);
	const images = [
		{
			original: "https://picsum.photos/id/1018/1000/600/",
			thumbnail: "https://picsum.photos/id/1018/250/150/",
		},
		{
			original: "https://picsum.photos/id/1015/1000/600/",
			thumbnail: "https://picsum.photos/id/1015/250/150/",
		},
		{
			original: "https://picsum.photos/id/1019/1000/600/",
			thumbnail: "https://picsum.photos/id/1019/250/150/",
		},
		{
			original: "src/image/portfolio/S2/mainImg.png",
			thumbnail: "src/image/portfolio/S2/mainImg.png",
		},
	];
	const modalVariants = {
		init: {
			opacity: 0,
			y: 500,
		},
		anim: {
			opacity: 1,
			y: 0,
		},
	};
	if (props.modalShow) {
		return (
			<>
				<div className='modalOverlay'>
					<motion.div
						className='modalContainer'
						variants={modalVariants}
						initial='init'
						animate='anim'>
						<div className='modalHeader'>
							<div className='burger' onClick={props.hideModal}>
								<div className={`line1`}></div>
								<div className={`line3`}></div>
							</div>
						</div>
						<div className='modalContent'>
							<div className='alertIcon'>
								<FontAwesomeIcon icon={faWrench} />
							</div>

							<div className='alertTitle'>
								Sorry, View Details feature is under
								construction!
							</div>
							<div className='alertSubTitle'>
								This awesome feature will soon be ready in the
								next patch👷🏽‍♂️.
							</div>
						</div>
					</motion.div>
				</div>
			</>
		);
	} else {
		return <></>;
	}
};

export default ProjectModal;
